.modal{
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}
.modal__content{
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    width: 25vw;
    height: 25vh;
    transform:scale(0.5);
    transition: 0.4s;

}
.modal.active{
     opacity: 1;
    pointer-events: all;
}
.modal__content.active{
transform: scale(1);
}
.Purple{
    background-color: #232234;
    opacity: 0.9;
}